.container {
    margin: 5px 5px;
    margin-left: 95px;
    padding: 0;
    
};
@media screen and (max-width: 768px) {
    .container{
        width:100%;
        margin: 10px 10px;
        overflow-x:'auto'
    }
}

@media screen and (max-width: 460px) {
    .container{
        width:100%;
        margin: 10px 10px;
        margin-left: 55px;
        overflow-x:'auto'
    }
}

.MuiTableRow-root:nth-child(even){
    background-color: #f2f2f2
}

.MuiTableRow-root:hover {
    background-color: #ddd;
}

.MuiTableRow-head th{
    border: 1px solid #ddd;
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #4CAF50;
    color: white;
}